<template>
  <CommonLayout>
    <div class="graph-box">
      <echartTimeline :date="sliderRef"></echartTimeline>
    </div>
    <div class="five-box">
      <dv-border-box-10 v-for="(_, index) in 5" :key="index">
        <rehabiliPlan 
          :title="exprehabitate[index]?.title" 
          :tips="exprehabitate[index]?.tips"
          :sections="exprehabitate[index]?.sections"
        />
      </dv-border-box-10>
    </div>
  </CommonLayout>
  <siderBar @sliderUpdated="sliderUpdated"></siderBar>
</template>

<script>
import drawMixin from "../utils/drawMixin";
import echartTimeline from '../components/graph/echartTimeline.vue';
import rehabiliPlan from "../components/graph/rehabilitationPlan.vue";
import CommonLayout from "../components/CommonLayout.vue";
import { formatTime } from "../utils/index";
import siderBar from "../components/graph/siderBar.vue";

const exprehabData = [
        {
            title: "",
            tips: {
              title: 'Tips1',
              content: ""
            },
            sections: [
              {
                title: "",
                items: [
                  " 10 - 15  8 - 10  3 ",
                  " 30  2 - 3 ",
                  " 15 - 30  10 - 15  3 ",
                  " 3 - 4  8 - 12  2 "
                ]
              },
              {
                title: "",
                items: [
                  " 20 - 30  2 - 3 ",
                  " 20 - 30  2 ",
                  " 30 - 45  2 - 3 "
                ]
              },
              {
                title: "",
                items: [
                  " 5 - 10  3 ",
                  " 10 - 15  2 - 3 ",
                  " 10 - 15  3 - 5 "
                ]
              }
            ]
          },
        {
            title: "",
            tips: {
              title: 'Tips1',
              content: ""
            },
            sections: [
              {
                title: "",
                items: [
                  " 10 - 15  3 ",
                  " 10 - 15  2 "
                ]
              },
              {
                title: "",
                items: [
                  " 3 - 5  10 - 15  3 ",
                  " 5 - 10  2 "
                ]
              },
              {
                title: "",
                items: [
                  " 5 - 10  3 ",
                  " 10 - 15  3 "
                ]
              }
            ]
          },
        {
            title: "",
            tips: {
              title: 'Tips1',
              content:""
            },
            sections: [
              {
                title: "",
                items: [
                  " 10 - 15  3 ",
                  " 5 - 10  3 ",
                  " 2 "
                ]
              },
              {
                title: "",
                items: [
                  " 15 - 20  2 ",
                  " 10 - 15  2 "
                ]
              },
              {
                title: "",
                items: [
                  " 5 - 10  3 ",
                  " 10 - 15  2 "
                ]
              }
            ]
          },
        {
            title: "",
            tips: {
              title: 'Tips1',
              content: ""
            },
            sections: [
              {
                title: "",
                items: [
                  " 10 - 15  2 ",
                  " 10 - 15  2 "
                ]
              },
              {
                title: "",
                items: [
                  " 10 - 15  2 "
                ]
              },
              {
                title: "",
                items: [
